import { Component, OnInit } from '@angular/core';
import { FbService } from './services/fb.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  friendData = [];
  arrNameFilter = [];
  arrPhoto = [];
  listTen = '';
  dataFilted = [];
  id = '';
  token = 'EAAAAUaZA8jlABALQLGkBwNQfzsURa0SU9C7FLiaylDQVs9AjXPxv36kRBa1Vpax4ofLIanoGvS67GE2GXbE4j9nT1feTGaYOudHOvZBJPZAH25PaqZCO6LLUq1dzplGMZAVZCLy53JxzjPviyjCjagqzQOU28b5gnsc9luJ3h4vwZDZD';

  constructor(private _fbService: FbService) {
  }

  ngOnInit(): void {

  }

  getData() {
    this._fbService.getFriendByUId(this.id, this.token).then(rs => {
      this.friendData = rs.data;
    });
  }


  find() {
    this.arrNameFilter = this.listTen.split('\n');
    const data = this.friendData.slice();
    this.dataFilted = data.filter(x => this.arrNameFilter.indexOf(x.name) >= 0);

    this.dataFilted.forEach(element => {
      this.getPhotoByUId(element);
    });

  }

  getPhotoByUId(element: any) {
    this._fbService.getPhotoByUId(element.id, this.token).then(rs => {
      element.photos = rs.data;
    });
  }

}
