import { Injectable, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';

export interface ResponseResult {
  status: boolean;
  message: string;
  error: string;
  data: any;
  totalRecord: number;
}

@Injectable()

export class FbService {
  injector: Injector;
  http: HttpClient;

  constructor(private _http: HttpClient, _injector: Injector) {
    this.injector = _injector;
    this.http = _http;
  }


  getFriendByUId(id: string, token: string): Promise<any> {
    const url = `https://graph.facebook.com/v3.1/${id}/friends?access_token=${token}&offset=0&fields=name%2Cid&limit=5000`;
    return this.http
      .get<ResponseResult>(url).toPromise();
  }


  getPhotoByUId(id: string, token: string): Promise<any> {
    const url = `https://graph.facebook.com/v3.1/${id}/photos?type=uploaded&fields=picture&limit=20&access_token=${token}`;
    return this.http
      .get<ResponseResult>(url).toPromise();
  }

}
